.standing-player {
  float: left; }

.standing-stats {
  float: right; }

.standing-count {
  position: absolute;
  top: 50%;
  left: -12px;
  width: 24px;
  height: 24px;
  display: block;
  background: #d9d9d9;
  color: #333333;
  border-radius: 30px;
  text-align: center;
  font-weight: 600;
  font-size: 0.8125rem;
  font-family: 'proxima-nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%); }
  .standing-count span {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    -o-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%); }

.standing-count--1 {
  width: 28px;
  height: 28px;
  left: -14px;
  background: #cca55c; }

.standing-count--2 {
  width: 26px;
  height: 26px;
  left: -13px;
  background: #C9D1D3; }

.standing-count--3 {
  width: 24px;
  height: 24px;
  left: -12px;
  background: #7A6248; }

.section {
